.header {
  position: relative;
  background-color: #191616;
  background-image: url(../images/header/bg-mob.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/header/bg-mob-2x.jpg);
  }

  padding-top: 80px;
  padding-bottom: 112px;
  margin: 0 auto;

  @media screen and (min-width: 768px) {
    background-image: url(../images/header/bg-tab.jpg);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/header/bg-tab-2x.jpg);
    }
    padding-top: 40px;
    padding-bottom: 170px;
  }
  @media screen and (min-width: 1280px) {
    background-image: url(../images/header/bg-desk.jpg);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/header/bg-desk-2x.jpg);
    }

    padding-bottom: 284px;
  }

  &__logo {
    position: absolute;
    top: -90px;
    width: 107px;
    height: 180px;
    clip-path: ellipse(107px 90px at 107px 90px);
    background-color: linear-gradient(
      136.07deg,
      rgba(196, 196, 196, 0.03) 10.75%,
      rgba(196, 196, 196, 0) 92.33%
    );
    backdrop-filter: blur(30px);
    transform: rotate(180deg);

    @media screen and (min-width: 768px) {
      top: -151px;
      width: 180px;
      height: 302px;
      clip-path: ellipse(180px 151px at 180px 151px);
    }
    @media screen and (min-width: 1280px) {
      top: -193px;
      width: 229px;
      height: 386px;
      clip-path: ellipse(229px 193px at 229px 193px);
    }
  }

  &__logo-icon {
    position: absolute;
    z-index: 1;
    top: 48;
    left: 30;
    fill: $main-white-color;
    transform: rotate(180deg);

    @media screen and (min-width: 768px) {
      width: 101px;
      height: 30px;
      top: 102px;
      left: 82px;
    }
    @media screen and (min-width: 1280px) {
      width: 138px;
      height: 73px;
      top: 91px;
      left: 66px;
    }
  }

  &__title {
    font-weight: 900;
    font-size: 18px;
    line-height: 1.22;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 24px;
    color: $accent-color;
    width: 220px;

    @media screen and (min-width: 768px) {
      font-size: 30px;
      line-height: 1.23;
      margin-bottom: 40px;
      width: 400px;
    }
    @media screen and (min-width: 1280px) {
      font-size: 50px;
      line-height: 1.22;
      margin-bottom: 48px;
      width: 608px;
    }
  }
}

.select__icon {
  position: absolute;
  top: 14;
  right: 20;
  fill: $main-gray-color;
}

