.search-form {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    justify-content: center;
  }

  &__element {
    position: relative;
    // backdrop-filter: blur(80px);

    width: 288px;
    height: 40px;
    font-size: 16px;
    line-height: 1.25;
    color: $main-gray-color;

    &:not(:last-child) {
      margin-bottom: 24px;
    }

    @media screen and (min-width: 768px) {
      width: 336px;

      &:not(:last-child) {
        margin-bottom: 0;
        margin-right: 32px;
      }
    }

    @media screen and (min-width: 1280px) {
      width: 288px;
    }
  }

  &__input {
    width: 100%;
    height: 100%;
    padding-left: 32px;
    border-radius: 20px;
    color: $main-white-color;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(20px);

    &:focus {
      border: 1px solid $main-white-color;
      outline: none;
    }
  }

  &__btn {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    right: 0;
    top: 50%;
    height: 40px;
    width: 60px;
    border: none;
    outline: none;
    border-radius: 0px 20px 20px 0px;
    background-color: transparent;
    transform: translateY(-50%);
  }

  &__icon {
    fill: $main-gray-color;
  }
  &__select {
    width: 100%;
    height: 100%;
    padding-left: 32px;
    border-radius: 20px;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(20px);
    border: none;

    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &:focus {
      border: 1px solid $main-white-color;
      outline: none;
    }
  }
}
